#intro-image{width:100%;height:550px;object-fit:cover;object-position:center}.intro-content{position:absolute;left:0;top:50%;transform:translateY(-50%);-ms-transform:translateY(-50%);background-color:#fff;border-radius:7px;padding:15px}.cuisine-item img{width:125px;height:125px;border-radius:50%;object-fit:cover}.order{box-shadow:0 0 0 1px rgba(67,41,163,0.08),0 1px 5px 0 rgba(67,41,163,0.08);border-radius:4px}.order:hover{box-shadow:0 0 0 1px rgba(255,0,24,0.17),0 1px 5px 0 rgba(255,0,24,0.12)}.order img{width:100%;height:100px;object-fit:cover;object-position:center}.banner-wrapper img{width:100%;height:100px;object-fit:cover;object-position:center}.banner-wrapper .text{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.top-rated{overflow:hidden}.top-rated:hover{cursor:pointer}.top-rated:hover img{transform:scale(1.2)}.top-rated img{width:100%;height:300px;object-fit:cover;object-position:center;transition:transform 0.5s}.top-rated .my-badge.bottom{position:absolute;left:15px;bottom:15px}.top-rated .my-badge.bottom:hover{background-color:#ff0018;color:#fff}.right-product-wrapper{position:absolute;top:0;height:100%;bottom:0;overflow:hidden;padding-bottom:20px;right:15px;left:15px}.right-product-wrapper .popular-right-image{height:100%;width:100%;max-width:100%;object-fit:cover;object-position:center}

.product{box-shadow:0 0 0 1px rgba(67,41,163,0.08),0 1px 5px 0 rgba(67,41,163,0.08);font-size:14px}.product:hover{cursor:pointer;box-shadow:0 0 0 1px rgba(255,0,24,0.17),0 1px 5px 0 rgba(255,0,24,0.12)}.product img{width:100%;height:150px;object-fit:cover}.product .add-to-wishlist{position:absolute;top:10px;right:10px;width:30px;height:30px;border-radius:50%;line-height:30px;text-align:center}.product .add-to-wishlist svg,.product .add-to-wishlist path{color:#fff;font-size:22px}.product .add-to-wishlist svg:hover,.product .add-to-wishlist path:hover{cursor:pointer;color:#ff0018}.product .point-badge{background-color:#4aa110;font-weight:300;color:#fff;padding:10px 7px;border-radius:4px;letter-spacing:1.5px;font-size:15px;line-height:15px}.product svg,.product path{color:#fcb71b}

*{font-family:"Poppins", sans-serif;color:rgba(0,0,0,0.870588)}body{overflow-x:hidden}a{text-decoration:none !important;color:inherit}.red-color{color:#ff0018}a.red-color:hover{color:#c30012}.dark-color{color:rgba(0,0,0,0.870588)}.red-btn{background-color:#ff0018;color:#fff;border-color:#ff0018}.red-btn:hover{background-color:#c30012;border-color:#c30012}.text-sm{font-size:14px}.navbar-light .navbar-nav .nav-link{color:#ff0018 !important}.transform-center{position:absolute;top:50%;left:0;right:0;transform:translate(0%, -50%);z-index:1}.outline-btn{background-color:transparent;color:#ff0018;border:1px solid rgba(67,41,163,0.2);font-size:15px}.outline-btn:hover{color:#ff0018;border-color:#ff0018 !important}.my-badge{background-color:rgba(255,255,255,0.5);padding:10px 15px;border-radius:7px;font-size:18px}

.footer{padding-top:100px}@media screen and (max-width: 991px){.footer{padding-top:20px}}.footer svg,.footer path{color:#ff0018}.footer .icon-wrapper{width:40px;height:40px;display:flex;align-items:center;justify-content:center;color:#ada3a3;transition:0.3s;text-decoration:none}.footer .footer-copy{border-top:1px solid rgba(67,41,163,0.2);font-size:14px}

