d;bottom:0;width:100%;background-color:#fff;z-index:99;box-shadow:0 0 0 1px rgba(67,41,163,0.1),0 1px 8px 0 rgba(67,41,163,0.1)}.mobile-menu .active div,.mobile-menu .active path,.mobile-menu .active svg{color:#ff0018}.float-cart-icon{position:fixed;bottom:75px;right:20px;background-color:#ff0018;width:60px;height:60px;text-align:center;border-radius:50%}.float-cart-icon svg,.float-cart-icon path{margin:20.5px 0;color:#fff;font-size:22px}

.flash-msg{position:fixed;bottom:0;width:100%;left:50%;transform:translateX(-50%);z-index:1051;min-width:150px;font-size:14px;background-color:#03d333;color:#fff;transition:all 0.5s}@media screen and (max-width: 767px){.flash-msg{top:0;bottom:unset;font-size:16px}}

.item{box-shadow:0 0 0 1px rgba(67,41,163,0.08),0 1px 5px 0 rgba(67,41,163,0.08);border-radius:4px}.item:hover{cursor:pointer;box-shadow:0 0 0 1px rgba(67,41,163,0.2),0 1px 5px 0 rgba(67,41,163,0.2);transition:0.5s}.item .image{width:150px;flex-basis:150px;background-size:cover;background-position:center}@media screen and (max-width: 767px){.item .image{width:100%;background-size:contain;background-repeat:no-repeat}}.item svg,.item path{color:#fcb71b}.coupon{border:1px solid rgba(0,0,0,0.125);border-radius:4px}.coupon .image{width:100%;min-height:250px;background-image:url(/food-delivery/static/media/coupon-image.ce771f47.jpg);background-position:center;background-size:cover;background-repeat:no-repeat}.coupon .title{font-size:48px}.coupon .subtitle{font-size:36px}.coupon .coupon-btn{background-color:#ff0018;border-color:#ff0018}.coupon .coupon-btn:hover{background-color:#c30012;border-color:#c30012}

.cart{box-shadow:0 0 0 1px rgba(67,41,163,0.08),0 1px 5px 0 rgba(67,41,163,0.08);position:-webkit-sticky;position:sticky;top:65px}.cart .order-list{max-height:400px;overflow-y:auto}.cart .order-list .cart-item{border-bottom:1px solid #ccc}.cart .order-list .name{font-size:14px;width:240px}@media screen and (max-width: 767px){.cart .order-list .name{width:65%}}@media screen and (min-width: 992px) and (max-width: 1599px){.cart .order-list .name{width:65%}}.cart .order-list .quantity{min-width:30px;flex-basis:30px;font-size:14px}.cart .order-list .price{text-align:right;min-width:50px;flex-basis:50px}.cart .order-list .delete:hover path,.cart .order-list .delete:hover svg{transition:color 0.5s;color:#ff0018;cursor:pointer}

.store .store-image{background-image:url("https://cdn.pixabay.com/photo/2015/05/31/11/23/table-791167_960_720.jpg");background-size:100% auto;background-repeat:no-repeat;background-size:cover;background-position:center;height:250px;width:100%;margin-bottom:15px}.store .title.small{font-size:24px}.store .stars svg,.store .stars path{color:#fcb71b}.store .banner-img{width:100%;height:120px;object-fit:cover;object-position:center;border-radius:4px}.store .banner-text{position:absolute;left:20px;right:20px;top:50%;transform:translateY(-50%)}.store .main{background-color:#f9f9f9}.store .store-info .store-name{font-size:36px}.store .store-info .icon-wrapper{width:40px;text-align:center}.store .store-info a:hover{color:#ff0018}.store .store-info .open .title{font-size:24px;background-color:rgba(0,0,0,0.03)}.store .store-info .open .day{font-size:13px}.item-modal img{width:100%;height:250px;object-fit:cover;object-position:center}.item-modal .close{position:absolute;top:5px;right:10px;outline:unset;box-shadow:unset}.item-modal .close span{color:#ff0018 !important;outline:unset;box-shadow:unset}.item-modal .name{font-size:24px}.item-modal .input-group{width:150px}.item-modal .input-group .input-group-prepend .input-group-text{border-top-left-radius:19px;border-bottom-left-radius:19px}.item-modal .input-group .input-group-append .input-group-text{border-top-right-radius:19px;border-bottom-right-radius:19px}.item-modal .input-group .input-group-text{background-color:#fff;cursor:pointer}.item-modal .input-group input{box-shadow:unset;outline:unset}.item-modal .add-to-cart{background-color:#ff0018;border-color:#ff0018}.item-modal .add-to-cart:hover{background-color:#c30012;border-color:#c30012}

